}
.image-slider-track{
  display: flex;
  width:calc(200px*12);
  animation: play 30s linear infinite;
}

@keyframes play{
  0%{
      transform: translateX(0);
  }

  100%{
      transform: translateX(calc(-250px*6));
  }
}

.image-slider::before,
.image-slider::after{
  background: linear-gradient(to right,rgba(255,255,255,1)0%,rgba(255,255,255,0)100%);
  content: '';
  height: 100%;
  width: 15%;
  z-index: 2;
  position: absolute;

  }

